import { useState } from 'react';
import axios from '../../utils/axios';

const Login = (props) => {
    const [state, setState] = useState({
        username: "",
        password: ""
    });
    const [loading, setLoading] = useState(false);
    const [message, setMessage] = useState('');

    const handleSubmit = e => {
        e.preventDefault();
        setLoading(true);
        axios.post('/login', state)
        .then(response => {
            let data = response.data;
            setLoading(false);
            if(data.status == 401) {
                setMessage(data.message);
            }
            if(data.status == 200) {
                setMessage('Success');
                localStorage.setItem('X-Auth-token', data.token);
                props.history.push('/')
            }
        })
        .catch(err => {
            console.log(err)
            alert(err.message || 'something went wrong');
            setLoading(false);            
        });
    }
    return (

        <div id="page-wrapper" >
            <div id="page-inner">
                <div className="row text-center" style={{display: "flex", justifyContent: 'center'}}>
                    <div className="col-lg-6 col-md-8 col-sm-10 col-xs-10 text-center">
                        <h3>Welcome Admin</h3>
                        <br />
                        <div className="panel panel-primary">
                            <div className="panel-heading">
                                Login
                            </div>
                            <div className="panel-body">
                                <form onSubmit={ e => handleSubmit(e) }>
                                    <div class="input-group">
                                        <span class="input-group-addon">@</span>
                                        <input type="text" class="form-control" placeholder="Username" onChange={e => {
                                            setState(state => {
                                                return {...state, username: e.target.value}
                                            })
                                        }} value={state.username} />
                                    </div>
                                    <br />
                                    <div class="input-group">
                                        <span class="input-group-addon">#</span>
                                        <input type="password" class="form-control" placeholder="Password" onChange={e => {
                                            setState(state => {
                                                return {...state, password: e.target.value}
                                            })
                                        }} value={state.password} />
                                    </div>
                                    <br />
                                    <div class="input-group text-center">
                                        <button className="btn btn-success" disabled={loading}>Submit</button>
                                    </div>
                                </form>

                            </div>
                            <div className="panel-footer">
                                {message || 'Enter your credentials'}
                            </div>
                        </div>
                    </div>
                </div>
            </div>
        </div>
    )
}

export default Login;